import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Gallery, Div, Button, platform, PanelHeaderContent, Cell, List, FormLayout, FormStatus, Snackbar, HeaderButton, IOS, Avatar, itemStyle} from '@vkontakte/vkui';
import connect from '@vkontakte/vk-connect';

import '@vkontakte/vkui/dist/vkui.css';

import author from '../author/author.json';
import vkuiConnect from '@vkontakte/vkui-connect';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24ReplyOutline from '@vkontakte/icons/dist/24/reply_outline';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Pin from '@vkontakte/icons/dist/24/pin';

import './books.css';

const blueBackground = {
  backgroundColor: 'var(--accent)'
};


class AuthorDetail extends React.Component {
	constructor (props) {
		super(props);

		this.state = {
			bookText: null,
			activeStory: "home",
			showButton: false,
			check: false,
			activePanel: 'feed',
      history: ['feed'],
			page: this.props.bookStartId,
		};
	}
	


    share = () => { 
	  	connect.send("VKWebAppShowWallPostBox", {"message": `Привет! А я узнал, что "${author[this.props.bookId].vosemwordfran}" - это "${author[this.props.bookId].vosemword}" Учи французкий и ты в сервисе - vk.com/app7116412`  });
	}

	backauthor = (e) => { 
		this.setState({ activeStory: e.currentTarget.dataset.to })
		window.scrollTo(0,0)
	}

	goBack = () => {
		const history = [...this.state.history];
		history.pop();
		const activePanel = history[history.length - 1];
		if (activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppDisableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  goForward = (activePanel) => {
		const history = [...this.state.history];
		history.push(activePanel);
		if (this.state.activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppEnableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }
	  showImg = () => {
        this.setState({showImg: true});
    };
      showButton = () => {
        this.setState({showButton: true});
    };

	
	render () {
		const osname = platform();
		return (
			<Panel id={this.props.id}>
				<PanelHeader 
			left={<HeaderButton onClick={this.props.backauthor} data-to="feed">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}>
			<PanelHeaderContent
        status={author[this.props.bookId].title}
        >
			Apprendre Le Français
		</PanelHeaderContent>
		</PanelHeader>
		   {this.state.showImg && <Div className="div-center">
      <FormStatus title="Отлично!" state="fd">
        Вы прошли карточку "{author[this.props.bookId].title}". При желании Вы можете повторить ее позже! 
   </FormStatus>
                    </Div>} 
		 <Group title="Слова">
              <List>
                <Cell selectable indicator={author[this.props.bookId].oneword}>{author[this.props.bookId].onewordfran}</Cell>
                <Cell selectable indicator={author[this.props.bookId].twoword}>{author[this.props.bookId].twowordfran}</Cell>
                <Cell selectable indicator={author[this.props.bookId].friword}>{author[this.props.bookId].friwordfran}</Cell>
                <Cell selectable indicator={author[this.props.bookId].fourword}>{author[this.props.bookId].fourwordfran}</Cell>
                <Cell selectable indicator={author[this.props.bookId].fiveword}>{author[this.props.bookId].fivewordfran}</Cell>
                <Cell selectable indicator={author[this.props.bookId].sixword}>{author[this.props.bookId].sixwordfran}</Cell>
                <Cell selectable indicator={author[this.props.bookId].sevenword}>{author[this.props.bookId].sevenwordfran}</Cell>
                <Cell selectable indicator={author[this.props.bookId].vosemword}>{author[this.props.bookId].vosemwordfran}</Cell>
                <Cell selectable indicator={author[this.props.bookId].devytword}>{author[this.props.bookId].devytwordfran}</Cell>
                <Cell selectable onChange={this.showButton} indicator={author[this.props.bookId].tenword}>{author[this.props.bookId].tenwordfran}</Cell>
              </List>
            </Group>    
    <Div>
       
       <Button before={<Icon24Note/>} onClick={this.share} size="xl" level="overlay_secondary">Поделиться на стене</Button>
     </Div> 
       
       {this.state.showButton && <Div className="div-center">     
       <Button before={<Icon24Pin/>} onClick={this.showImg} size="xl" level="overlay_secondary">Закончить!</Button>
     </Div>} 
                      
			</Panel>
        );
    }
}
AuthorDetail.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	backauthor: PropTypes.func.isRequired,
	share: PropTypes.func,
};


export default (AuthorDetail);
