function openTab(url) {

// Create link in memory

let a = window.document.createElement("a");

a.target = '_blank';

a.href = url;

// Dispatch fake click

let e = window.document.createEvent("MouseEvents");

e.initMouseEvent("click", true, true, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);

a.dispatchEvent(e);

};

class BookDetail extends React.Component {
	constructor (props) {
		super(props);

		this.state = {
			bookText: null,
			activeStory: "home",
			check: false,
			formLoaded: false,
			popout: null,
			showButton: false, 
			contextOpened: false,
            mode: 'all',
			activePanel: 'feed',
            history: ['feed'],
			page: this.props.bookStartId
		};
	}

	goBack = () => {
		const history = [...this.state.history];
		history.pop();
		const activePanel = history[history.length - 1];
		if (activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppDisableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  share = () => { 
	  	connect.send("VKWebAppShowWallPostBox", {"message": `Привет! А я узнал, что "${books[this.props.bookId].vosemwordfran}" - это "${books[this.props.bookId].vosemword}" Учи французкий и ты в сервисе - vk.com/app7116412`  });
	}

	toggleContext () {
       this.setState({ contextOpened: !this.state.contextOpened });
      }

    select (e) {
       const mode = e.currentTarget.dataset.mode;
       this.setState({ mode });
       requestAnimationFrame(this.toggleContext);
      }

	  goForward = (activePanel) => {
		const history = [...this.state.history];
		history.push(activePanel);
		if (this.state.activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppEnableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  go = (e) => { 
		this.setState({ activePanel: e.currentTarget.dataset.to })
		};
	
		backauthor = (e) => { 
			this.setState({ activeStory: e.currentTarget.dataset.to })
			window.scrollTo(0,0)
		}
    showImg = () => {
        this.setState({showImg: true});
    };
      showButton = () => {
        this.setState({showButton: true});
    };



		

	getBookText = (id,glava) => {
		console.log(require("../books"+books[id].preview))
		fetch(require("../books/content"+require("../books"+books[id].preview)[glava].text))
		.then(text => text.text())
		.then(data => {this.setState({bookText: data})})
		this.setState({page: glava, check: true})
		let status = window.navigator.onLine ? true : false;
if(!status) {alert("Проверьте интернет соединение.\nПовторите попытку");}
	}
	
	
	render () {
		const osname = platform();
		return (
			<Panel id={this.props.id}>
				    <PanelHeader
            left={<HeaderButton onClick={this.props.go} data-to="feed" > {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}
          >
              <PanelHeaderContent
        status={books[this.props.bookId].title}
      
     >
             Apprendre Le Français
           </PanelHeaderContent>
          </PanelHeader>
				  {this.state.showImg && <Div className="div-center">
      <FormStatus title="Отлично!" state="fd">
        Вы выучили карточку "{books[this.props.bookId].title}". При желании Вы можете повторить ее позже! 
   </FormStatus>
                    </Div>} 
				<Group> 
              <List>
                <Cell selectable indicator={books[this.props.bookId].oneword}>{books[this.props.bookId].onewordfran}</Cell>
                <Cell selectable indicator={books[this.props.bookId].twoword}>{books[this.props.bookId].twowordfran}</Cell>
                <Cell selectable indicator={books[this.props.bookId].friword}>{books[this.props.bookId].friwordfran}</Cell>
                <Cell selectable indicator={books[this.props.bookId].fourword}>{books[this.props.bookId].fourwordfran}</Cell>
                <Cell selectable indicator={books[this.props.bookId].fiveword}>{books[this.props.bookId].fivewordfran}</Cell>
                <Cell selectable indicator={books[this.props.bookId].sixword}>{books[this.props.bookId].sixwordfran}</Cell>
                <Cell selectable indicator={books[this.props.bookId].sevenword}>{books[this.props.bookId].sevenwordfran}</Cell>
                <Cell selectable indicator={books[this.props.bookId].vosemword}>{books[this.props.bookId].vosemwordfran}</Cell>
                <Cell selectable indicator={books[this.props.bookId].devytword}>{books[this.props.bookId].devytwordfran}</Cell>
                <Cell selectable onChange={this.showButton} indicator={books[this.props.bookId].tenword}>{books[this.props.bookId].tenwordfran}</Cell>
              </List>
            </Group>  
               <Div>
       <Button before={<Icon24Note/>} onClick={this.share} size="xl" level="overlay_secondary">Поделиться на стене</Button>
     </Div>     
           {this.state.showButton && <Div className="div-center">     
       <Button before={<Icon24Pin/>} onClick={this.showImg} size="xl" level="overlay_secondary">Закончить!</Button>
     </Div>}        
			</Panel>
        );
    }
}
BookDetail.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


export default (BookDetail);

