import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Radio, Gallery, Div, Button, platform, ScreenSpinner, PanelHeaderContent, Separator, Cell, List, FormLayout, FormStatus, Snackbar, HeaderButton, IOS, Avatar, itemStyle} from '@vkontakte/vkui';
import connect from '@vkontakte/vk-connect';

import '@vkontakte/vkui/dist/vkui.css';

import books from '../books/books.json';
import vkuiConnect from '@vkontakte/vkui-connect';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24ReplyOutline from '@vkontakte/icons/dist/24/reply_outline';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24Pin from '@vkontakte/icons/dist/24/pin';
import Icon24Done from '@vkontakte/icons/dist/24/done';

import './books.css';

const blueBackground = {
  backgroundColor: 'var(--accent)'
};


class AuthorDetailvopros extends React.Component {
	constructor (props) {
		super(props);

		this.state = {
			bookText: null,
			activeStory: "home",
			check: false,
			popout: null, 
			showImg: true, 
			Button: true, 
			activePanel: 'feed',
      history: ['feed'],
			page: this.props.bookStartId,
		};
	}
	

	backauthor = (e) => { 
		this.setState({ popout: <ScreenSpinner /> });
        setTimeout(() => { this.setState({ popout: null }) }, 1500);
		this.setState({ activeStory: e.currentTarget.dataset.to })
		window.scrollTo(0,0)
	}

	goBack = () => {
		const history = [...this.state.history];
		history.pop();
		const activePanel = history[history.length - 1];
		if (activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppDisableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }

	  goForward = (activePanel) => {
		const history = [...this.state.history];
		history.push(activePanel);
		if (this.state.activePanel === 'feed') {
		  vkuiConnect.send('VKWebAppEnableSwipeBack');
		}
		this.setState({ history, activePanel });
	  }
	  showImg = () => {
        this.setState({Button: false});
        this.setState({showImg: false});
        this.setState({quest1: true});
    };  quest1
        quest1 = () => {
        this.setState({Button: false});
        this.setState({showImg: false});
        this.setState({fail: false});
    };
         quest2 = () => {
        this.setState({quest1: false});
        this.setState({quest2: true});
        this.setState({fail: false});
    };
         quest3 = () => {
        this.setState({quest2: false});
        this.setState({quest3: true});
        this.setState({fail: false});
    };
         quest4 = () => {
        this.setState({quest3: false});
        this.setState({quest4: true});
        this.setState({fail: false});
    };
         quest5 = () => {
        this.setState({quest4: false});
        this.setState({quest5: true});
        this.setState({fail: false});
    };
    fail = () => {
        this.setState({fail: true});
       this.setState({finish: false});
    };
      finish = () => {
        
        this.setState({finish: true});
        this.setState({fail: false});
    };
 

	render () {
		const osname = platform();
		return (
			<Panel id={this.props.id}>
				<PanelHeader 
			left={<HeaderButton onClick={this.props.backauthor} data-to="test">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}>
			<PanelHeaderContent
        status={books[this.props.bookId].title}
        >
			Apprendre Le Français
		</PanelHeaderContent>
		</PanelHeader>
          {this.state.fail && <Div className="div-center">
      <FormStatus title="У-упс!" state="">
       Ошибка! Посмотри внимательней! 
   </FormStatus>
                    </Div>} 
         {this.state.showImg && 
        	<Group> <Div className="div-center">
        Проверьте себя! Ответьте на 5 вопросов по теме "{books[this.props.bookId].title}" и пройдите тест! 
      </Div></Group>}
          {this.state.finish && <Div className="div-center">
      <FormStatus title="Замечательно!" state="">
       Ты успешно прошел тест, пробуй проходить другие карточки, у тебя получится!
   </FormStatus>
                    </Div>} 
{this.state.Button && <Div className="div-center">
      <Button before={<Icon24Done/>} onClick={this.showImg} size="xl" level="commerce">Начать</Button>
 </Div>}
{this.state.quest1 && 
        	<Group> <Div className="div-center">
        Слово "{books[this.props.bookId].sixwordfran}" переводится, как: 
         <Separator style={{ margin: '12px 0' }} />
         <Radio name="radio" value="1" onChange={this.quest2} >{books[this.props.bookId].sixword}</Radio>
         <Radio name="radio" onChange={this.fail} value="2">{books[this.props.bookId].twoword}</Radio>
         <Radio name="radio" value="3" onChange={this.fail} >{books[this.props.bookId].tenword}</Radio>
      </Div></Group>}
{this.state.quest2 && 
        	<Group> <Div className="div-center">
        Слово "{books[this.props.bookId].tenwordfran}" переводится, как: 
         <Separator style={{ margin: '12px 0' }} />
         <Radio name="radio" value="1" onChange={this.fail} >{books[this.props.bookId].sixword}</Radio>
         <Radio name="radio" onChange={this.fail} value="2">{books[this.props.bookId].fiveword}</Radio>
         <Radio name="radio" value="3" onChange={this.quest3}>{books[this.props.bookId].tenword}</Radio>
    </Div> </Group>}
      {this.state.quest3 && 
        	<Group> <Div className="div-center">
        Слово "{books[this.props.bookId].twowordfran}" переводится, как: 
         <Separator style={{ margin: '12px 0' }} />
         <Radio name="radio" value="1" onChange={this.fail}>{books[this.props.bookId].sevenword}</Radio>
         <Radio name="radio" onChange={this.quest4} value="2">{books[this.props.bookId].twoword}</Radio>
         <Radio name="radio" value="3"onChange={this.fail} >{books[this.props.bookId].tenword}</Radio>
      </Div></Group>}
      {this.state.quest4 && 
        	<Group> <Div className="div-center">
        Слово "{books[this.props.bookId].vosemwordfran}" переводится, как: 
         <Separator style={{ margin: '12px 0' }} />
         <Radio name="radio" value="1" onChange={this.quest5} >{books[this.props.bookId].vosemword}</Radio>
         <Radio name="radio" onChange={this.fail} value="2">{books[this.props.bookId].sixword}</Radio>
         <Radio name="radio" value="3"onChange={this.fail} >{books[this.props.bookId].tenword}</Radio>
      </Div></Group>}
      {this.state.quest5 && 
        	<Group> <Div className="div-center">
        Слово "{books[this.props.bookId].fourwordfran}" переводится, как: 
         <Separator style={{ margin: '12px 0' }} />
         <Radio name="radio" value="1" onChange={this.finish} >{books[this.props.bookId].fourword}</Radio>
         <Radio name="radio" onChange={this.fail} value="2">{books[this.props.bookId].sevenword}</Radio>
         <Radio name="radio" value="3"onChange={this.fail} >{books[this.props.bookId].tenword}</Radio>
      </Div></Group>}




   
                      
			</Panel>
        );
    }
}
    AuthorDetailvopros.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	backauthor: PropTypes.func.isRequired,
	share: PropTypes.func,
};


export default (AuthorDetailvopros);
